import React, { Component } from 'react';
import './Counts.css';

const allAdds = [
  [
    [" ", " 13 800 106"],
    ["", " 8 481 376"],
    ["   ", " 3 307 619"]
  ],
  [
    [" ", " 2 831 737"],
    ["  ", " 2 538 398"],
    ["", " 2 011 483"]
  ],
  [
    ["", " 919 989"],
    ["", " 412 148"],
    ["", " 366 692"]
  ],
  [
    [" ", " 299 091"]
  ]
];

const regionAdds = [
  [
    ["", " 7 438 757"],
    [" .", " 1 409 082"],
    ["-", " 3 641 217"],
    [" .", " 106 625"],
    [" ", " 346 266"],
    ["", " 788 294"]
  ],
  [
    [" .", " 582 533"],
    [" .", " 512 177"],
    [" .", " 345 039"],
    [" .", " 405 763"],
    [" .", " 381 135"],
    [" ", " 1 897 277"]
  ],
  [
    [" ", " 449 956"],
    [" .", " 953 359"],
    [" .", " 640 329"],
    [" .", " 461 973"],
    [" ", " 643 803"],
    [" .", " 1 074 092"]
  ],
  [
    [" .", " 816 886"],
    [" .", " 486 878"],
    [" .", " 1 151 796"],
    [" ", " 424 033"],
    ["", " 1 065 059"],
    [" .", " 948 130"]
  ]
];

class Counts extends Component {
  renderList(adds) {
    return (
      <ul>
        {adds.map(add =>
          <li key={add[0]}>
            <a>{add[0]}</a>
            <span className="c-2 catalog-counts__number">{add[1]}</span>
          </li>)}
      </ul>
    );
  }

  render () {
    return (
      <div className="catalog-counts">
        <div className="catalog-counts__section">
          <div className="catalog-counts__row clearfix">
            {this.renderList(allAdds[0])}
            {this.renderList(allAdds[1])}
            {this.renderList(allAdds[2])}
            {this.renderList(allAdds[3])}
          </div>
        </div>
        <div className="catalog-counts__section">
          <div className="catalog-counts__switches">
            <span className="catalog-counts__switch catalog-counts__switch_on"> </span>&nbsp;
            <span className="catalog-counts__switch pseudo-link"></span>
          </div>
          <div className="catalog-counts__row clearfix">
            {this.renderList(regionAdds[0])}
            {this.renderList(regionAdds[1])}
            {this.renderList(regionAdds[2])}
            {this.renderList(regionAdds[3])}
          </div>
        </div>
      </div>
    );
  }
}

export default Counts;



