import React, { Component } from 'react';
import './Categories.css';

const categories = [
  [
    "",
    "",
    "  ",
    "  ",
    " ",
    "  "
  ],
  [
    "",
    "",
    "",
    ", , ",
    " ",
    "  ",
    " ",
    "  "
  ],
  [
    "",
    "",
    "",
    "",
    " "
  ],
  [
    " ",
    ", , ",
    "   ",
    "    ",
    "  ",
    "  "
  ],
  [
    "   ",
    " ",
    "  ",
    "    ",
    " ",
    "  ",
    "",
    " ",
    " ",
    "  "
  ],
  [
    " ",
    "  ",
    ",   ",
    " ",
    "",
    "  ",
    "   ",
    "",
    "  ",
    ""
  ],
  [
    "  ",
    "  ",
    "",
    "  ",
    "",
    " ",
    "  ",
    "  "
  ]
  ,
  [
    "",
    "",
    "",
    "",
    "",
    " ",
    "  "
  ]
];

class Categories extends Component {

  renderSection (section, index) {
    return (
      <dl key={index} className="category-map__section">
        {section.map((item, index) =>
          <dt key={item} className={`category-map__category ${!index || item === " " || item === "" ? "category-map__root" : ""}`}>
            <a>{item}</a>
          </dt>)}
      </dl>
    );
  }

  render () {
    return (
      <div className="category-map">
        {categories.map((section, index) => this.renderSection(section, index))}
      </div>
    );
  }
}

export default Categories;



